import React from 'react'

const Home = () => {
  return (
    <div className='home-container'>
      <img alt='logo' className='home-logo-img' src='/logo-transparent.png' />
      <p>A Markdown-enabled note-taking application</p>
    </div>
  )
}

export default Home



